/**
* CSS file to hold styles that apply across the application
*/

@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap");

html,
body {
  font-family: "Montserrat", sans-serif;
  height: 100%;
  margin: 0;
  overflow-x: hidden;
  padding: 0;
  width: 100%;
}

div#main {
  display: flex;
  flex-direction: column;
  width: 100%;
}

section {
  position: relative;
  width: 100%;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  width: 100%;
  object-fit: cover;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
}

h1 {
  font-family: "Cormorant Garamond", serif;
  font-size: 6rem;
  font-weight: 400;
  line-height: 1;
  margin: 0;
  text-align: center;
}

h2 {
  font-size: 2rem;
  font-weight: 400;
  margin: 0;
  padding-top: 1rem;
  text-align: center;
}

h3 {
  font-weight: 400;
  font-size: 1.25rem;
  margin: 0;
  text-align: center;
}

h3:hover {
  color: brown;
}

p {
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 0;
  font-weight: 300;
  color: seashell;
  text-align: center;
}

p.large {
  font-size: 24px;
  color: seashell;
}

p.small {
  font-size: 15px;
  font-weight: 400;
  color: rgb(39, 37, 2);
}

hr {
  margin: 2.5rem;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

li {
  line-height: 1.7;
  color: seashell;
}

li::marker {
  color: whitesmoke;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  color: #151d41;
}

.container {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 20px auto;
  padding: 0 15px;
}

.container .box {
  border-radius: 10px;
  border: 1px solid rgb(204, 204, 204);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  text-align: center;
}

img.socialIcon {
  height: 30px;
  width: 30px;
}

img.socialIcon:hover {
  filter: grayscale(100%) brightness(0);
}

/* Responsive Design */

/* For Large Screens like Desktops (width greater than 1200px) */
@media screen and (min-width: 1200px) {
  h1 {
    font-size: 6rem;
  }

  h2 {
    font-size: 1.75rem;
  }

  .container {
    grid-template-columns: 1fr 1fr 1fr;
  }

  .container .box {
    padding: 28px 20px;
  }
}

/* For Tablets (screen width between 768px and 1199px) */
@media screen and (max-width: 1199px) and (min-width: 768px) {
  h1 {
    font-size: 5rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  .container {
    grid-template-columns: 1fr 1fr;
  }

  .container .box {
    padding: 24px 18px;
  }

  p {
    font-size: 16px;
  }

  img.socialIcon {
    height: 25px;
    width: 25px;
  }
}

/* For Smaller Tablets and Large Phones (screen width between 480px and 767px) */
@media screen and (max-width: 767px) and (min-width: 480px) {
  h1 {
    font-size: 4rem;
  }

  h2 {
    font-size: 1.25rem;
  }

  .container {
    grid-template-columns: 1fr;
  }

  .container .box {
    padding: 20px 15px;
  }

  p {
    font-size: 15px;
  }

  img.socialIcon {
    height: 22px;
    width: 22px;
  }

  hr {
    width: 90%;
  }
}

/* For Small Phones (screen width less than 480px) */
@media screen and (max-width: 479px) {
  h1 {
    font-size: 3rem;
  }

  h2 {
    font-size: 1.2rem;
  }

  p {
    font-size: 14px;
  }

  .container {
    grid-template-columns: 1fr;
    padding: 0 10px;
  }

  .container .box {
    padding: 18px 12px;
  }

  img.socialIcon {
    height: 20px;
    width: 20px;
  }

  hr {
    width: 95%;
  }
}
